.transparent-btn a {
  display: block;
  font-size: 15px;
  line-height: 55px;
  height: 52px;
  text-align: center;
  padding: 0px;
  color: #2a4d8c;
  min-width: 236px;
  border-radius: 32px;
  -webkit-box-shadow: 0 0 0 2px #2a4d8c inset;
  box-shadow: 0 0 0 2px #2a4d8c inset;
  -webkit-transition: all ease-in-out 0.9s;
  -o-transition: all ease-in-out 0.9s;
  transition: all ease-in-out 0.9s;
}

.transparent-btn a:hover {
  background: #2a4d8c;
  color: #fff;
  text-decoration: none;
  outline: none;
}

.blue-btn a {
  display: block;
  font-size: 15px;
  height: 52px;
  line-height: 55px;
  border-radius: 32px;
  text-align: center;
  background: #074cc4;
  color: #fff;
  min-width: 236px;
  -webkit-transition: all ease-in-out 0.9s;
  -o-transition: all ease-in-out 0.9s;
  transition: all ease-in-out 0.9s;
}

.blue-btn a:hover {
  color: #fff;
  background: #2a4d8c;
  text-decoration: none;
  outline: none;
  cursor: pointer;
}

.btn-default {
  -webkit-transition: all ease-in-out 0.9s;
  -o-transition: all ease-in-out 0.9s;
  transition: all ease-in-out 0.9s;
}

.btn-default[disabled] {
  background-color: #6e6e6e;
}

.btn-default[disabled]:hover {
  color: #fff;
  background-color: #6e6e6e;
  /* border-color: #adadad; */
  /* transition: all ease-in-out 0.9s; */
}

.btn-default:hover {
  color: #fff;
  background-color: #2a4d8c;
  -webkit-transition: all ease-in-out 0.9s;
  -o-transition: all ease-in-out 0.9s;
  transition: all ease-in-out 0.9s;
}

.small-transparent-btn a {
  display: block;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
  padding: 4px 20px 2px 20px;
  color: #074cc4;
  margin-top: -4px;
  border-radius: 20px;
  -webkit-box-shadow: 0 0 0 1px #074cc4 inset;
  box-shadow: 0 0 0 1px #074cc4 inset;
  -webkit-transition: all ease-in-out 0.6s;
  -o-transition: all ease-in-out 0.6s;
  transition: all ease-in-out 0.6s;
}

.small-transparent-btn a:hover {
  color: #222;
  background: none;
  text-decoration: none;
  outline: none;
  -webkit-box-shadow: 0 0 0 1px #222 inset;
          box-shadow: 0 0 0 1px #222 inset;
  -webkit-transition: all ease-in-out 0.6s;
  -o-transition: all ease-in-out 0.6s;
  transition: all ease-in-out 0.6s;
}


